.slide-content {
	width: 100vw;
	//height: 100vh;
	flex-shrink: 0;
	overflow-y: hidden;
	
	box-sizing: border-box;
	
	display: flex;
	flex-direction: column;
	align-items: stretch;
	
	position: relative;
	
	
	background: $white;
	
	.slide-content--wrapper {
		display: flex;
		flex-direction: row;
		height: 100vh;
		
		position: relative;
	}
	
	.slide-content__section {
		padding-top: 80px;
		//height: 100%;
		width: auto;
		flex-shrink: 0;
		
		background: $white;
		color: $content-light-default;
		
		//margin-right: 25px;
	}
	
	.dark-ocean-bg {
		background: $dark-ocean;
		color: $white;
	}
	
	h2 {
		font-family: $title-ff;
		font-size: 34px;
		font-weight: 600;
		line-height: 1.3;
		letter-spacing: -0.024em;
	}
	
	.slide-content__slide {
		//display: flex;
		flex-direction: row;
		align-items: stretch;
		
		//position: absolute;
		//
		//top: 0;
		//bottom: 0;
		//left: 0;
		
		display: none;
		//opacity: 0;
		//visibility: hidden;
		//transition: opacity $trans-slow-half, visibility $trans-slow-half, display $trans-slow-half;
	}
	
	.slide--current {
		animation-name: fade;
		animation-duration: $trans-slow-half;
		display: flex;
		//opacity: 1;
		//visibility: visible;
	}
}




.article-blue {
	background: $sgs-blue;
	color: white;
	//font-size: 24px;
	font-size: 3.4vh;
	line-height: 1.4;
	
	font-weight: 400;
	
	min-width: 420px;
	width: 58.33vh;
	
	height: 100%;
	
	display: flex;
	align-items: center;
	padding: 8.333vh;
	
	p {
		display: block;
		margin: 0;
	}
}