.reveal--to-left {
	transition: 800ms;
	transform: translateX(40px);
	opacity: 0;
}

.reveal--to-right {
	transition: 800ms;
	transform: translateX(-40px);
	opacity: 0;
}

.custom-ul {
	.reveal--to-left.visible {
		transition-duration: 600ms;
		
		&:nth-of-type(1) { transition-delay: 100ms }
		&:nth-of-type(2) { transition-delay: 200ms }
		&:nth-of-type(3) { transition-delay: 300ms }
		&:nth-of-type(4) { transition-delay: 400ms }
	}
}

.reveal--to-top {
	transition: 800ms;
	transform: translateY(20%);
	opacity: 0;
}

.s2__custom-list {
	.reveal--to-top.visible {
		transition-duration: 800ms;
		
		&:nth-of-type(1) { transition-delay: 0ms }
		&:nth-of-type(2) { transition-delay: 50ms }
		&:nth-of-type(3) { transition-delay: 100ms }
		&:nth-of-type(4) { transition-delay: 150ms }
		&:nth-of-type(5) { transition-delay: 200ms }
		&:nth-of-type(6) { transition-delay: 250ms }
		&:nth-of-type(7) { transition-delay: 300ms }
	}
}

.reveal--to-bot {
	transition: 800ms;
	transform: translateY(-20%);
	opacity: 0;
}

.reveal--fade {
	transition: 1000ms;
	opacity: 0;
}

.visible {
	opacity: 1;
	transform: translateX(0) translateY(0);
	//background: palevioletred !important;
	
	.list-item__number {
		svg {
			path {
				animation: dash 2500ms linear forwards;
				animation-delay: 800ms;
			}
		}
	}
}

.s3-ch4__list {
	.visible {
		svg {
			path {
				animation: dash 2000ms linear forwards;
				animation-delay: 800ms;
			}
		}
	}
;
}