.main-screen__contacts {
	height: 80px;
	box-sizing: border-box;
	flex-shrink: 0;
	
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	
	.contacts__phone-number {
		font-size: 18px;
		letter-spacing: .2px;
		
		span {
			opacity: 0.4;
		}
		
		a {
			color: white;
			text-decoration: none;
			display: inline;
			
			transition: $trans-slow-half;
			
			&:hover {
				opacity: .5;
			}
		}
	}
	
	.contacts__email {
		box-sizing: border-box;
		height: 100%;
		width: 80px;
		font-size: 0;
		
		padding-left: 28px;
		padding-right: 28px;
		
		display: flex;
		align-items: center;
		justify-content: center;
		
		border-radius: 0;
		border: 0;
		
		background: url('../../../static/images/email-icon.svg') no-repeat center center / 30%, transparent;
		border-left: 1px solid rgba($color: white, $alpha: .16);
	}
}