.content__slide-1 {
	.slide-1--s1 {
		padding: 110px 100px 100px;
		//width: 50%;
		width: 700px;
		
		p {
			object-fit: contain;
		}
	}
	
	.slide-1--s2 {
		display: grid;
		padding-top: 0;
		
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr 1fr;
		
		width: calc(720px + 16vh);
		
		.content__image-wrapper {
			grid-row-start: 1;
			grid-row-end: 3;
			
			//background: rgba(crimson, .15);
			
			padding-left: 80px;
			padding-right: 80px;
			
			display: flex;
			justify-content: center;
			align-items: center;
			
			border-bottom: 1px solid rgba(black, .1);
			
			img {
				object-fit: contain;
				width: 100%;
				height: 100%;
			}
		}
		
		.content__text {
			padding: 68px 80px;
		}
	}
	
	.slide-1--s3 {
		width: 133.33333333vh;
		padding-top: 0;
		display: grid;
		//grid-gap: 4px;
		grid-template-columns: 1fr 1fr 1fr 1fr;
		grid-template-rows: 1fr 1fr 1fr;
		
		.grid-child {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			
			//background: rgba(crimson, .15);
			
			img {
				display: block;
				object-fit: contain;
			}
		}
		
		.grid-child--1 {
			grid-column-start: 1;
			grid-column-end: 4;
			
			padding: 0 80px;
			
			h1 {
				margin: 0;
			}
		}
		
		// Gonna be GRID after practice
	}
}