.slide-3__chapter-1 {
	//width: 5000px;
	
	//display: flex;
	//flex-direction: row;
	//height: 100%;
	
	
	// SECTION 1
	.s3-ch1__directions {
		//min-width: 2000px;
		position: relative;
		
		padding-right: 4vh;
		
		
		padding-left: 140px;
		
		.s3-ch1__directions-list {
			display: flex;
			flex-direction: row;
		}
		
		h3 {
			margin-bottom: 10.4vh;
			font-family: $title-ff;
		}
		
		.dir-list__item {
			display: flex;
			flex-direction: column;
			
			margin-right: 8.888vh;
			
			width: 60vh;
			
			h4 {
				font-family: $title-ff;
				//color: $sgs-blue;
				font-size: 18px;
				margin: 0;
				display: block;
				
				padding-right: 2.5vh;
			}
		}
		
		.dir-list__visual {
			position: relative;
			overflow: hidden;
			margin-bottom: 2.5vh;
			
			width: 100%;
			height: 38.888vh;
			
			background: #000;
			
			.dir__cover {
				display: block;
				top: 0;
				left: 0;
				
				width: 100%;
				height: 100%;
				
				object-fit: cover;
				position: relative;
				z-index: 1;
			}
			
			svg {
				display: block;
				position: absolute;
				z-index: 50;
				
				height: 50%;
				
				//background: lightcoral;
				
				top: 25%;
				left: 15%;
				
				path {
					fill-opacity: 1;
					fill: white;
					opacity: 1;
					
					stroke: white;
				}
			}
		}
	}
	
	
	// SECTION 2
	.s3-ch1__article {
		background: $content-dark;
		min-width: 720px;
		width: 100vh;
		
		padding-top: 0;
		
		
		display: grid;
		
		grid-template-rows: 50vh 50vh;
		grid-template-columns: 1fr;
	}
	
	.s3-ch1__article-image {
		overflow: hidden;
		position: relative;
		
		img {
			display: block;
			object-fit: cover;
			
			position: relative;
			
			width: 100%;
			height: 100%;
		}
	}
	
	.s3-ch1__article-text {
		padding: 5vh 11vh;
		
		color: $content-light-subtle;
		line-height: 1.77;
		
		display: flex;
		flex-direction: column;
		justify-content: center;
		
		p {
			margin: 0 0 30px;
		}
	}
	
	// SECTION 3
	
	.s3-ch1__experience {
		//margin-right: 100px;
		
		display: flex;
		flex-direction: row;
		align-items: stretch;
		padding-top: 0;
		
		//background: lightgoldenrodyellow;
		
		//padding: 80px 11vh 50px 11vh;
		
		.article-blue__wrapper {
			padding: 80px 0 80px 11vh;
		}
		
		.article-blue {
			margin-right: 11vh;
		}
		
		
	}
}