.content__slide-2 {
	h2 {
		color: $ocean;
		font-size: 48px;
		font-weight: bold;
		letter-spacing: -0.024em;
		margin: 0;
		line-height: 1.1;
		
		//background: orange;
	}
	
	
	// SECTION 1
	.slide-2--s1 {
		padding-top: 0;
		
		display: grid;
		//grid-gap: 4px;
		
		grid-template-rows: 50% 50%;
		//grid-template-columns: 650px repeat(5, 360px);
		grid-template-columns: 650px repeat(6, 50vh);
		
		//background: antiquewhite;
		
		
		.grid-child {
			//background: rgba(crimson, .25);
			
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: center;
		}
		
		.grid-child--title {
			grid-column-start: 1;
			grid-column-end: 2;
			
			grid-row-start: 1;
			grid-row-end: 1;
		}
		
		.grid-child--image-top {
			grid-column-start: 2;
			grid-column-end: 3;
			
			grid-row-start: 1;
			grid-row-end: 1;
			
			//background: palevioletred;
			
			position: relative;
			
			//overflow: hidden;
			
			//clip-path: polygon(100% 0, 0 0, 100% 100%);
			
			.image-top__deco {
				position: absolute;
				top: 30%;
				left: 15%;
				//transform: translate(-75%, -25%);
				
				width: 25vh;
				height: 25vh;
				background: $sgs-blue;
				z-index: 10;
				clip-path: polygon(100% 0, 0 0, 100% 100%);
			}
			
			.img-wrapper {
				height: 100%;
				width: 100%;
				clip-path: polygon(100% 0, 0 0, 100% 100%);
			}
			
			img {
				display: block;
				height: 150%;
				width: 200%;
				object-fit: cover;
				
				position: relative;
				
				//transition: transform $trans-fast;
			}
		}
		
		.grid-child--image-bottom {
			grid-column-start: 1;
			grid-column-end: 2;
			
			grid-row-start: 2;
			grid-row-end: 2;
			
			//background: palegoldenrod;
			
			display: flex;
			justify-content: flex-end;
			
			//overflow: hidden;
			
			.img-wrapper {
				height: 50vh;
				width: 50vh;
				margin-right: 0;
				margin-left: auto;
				clip-path: polygon(100% 0, 0 0, 100% 100%);
			}
			
			img {
				height: 100%;
				width: 200%;
				left: -25%;
				position: relative;
				object-fit: cover;
				
				//transition: transform $trans-fast;
			}
		}
		
		.grid-child--list-item {
			display: grid;
			grid-template-columns: 41.6% 1fr ;
			grid-template-rows: 100%;
			
			
			.list-item__number, .list-item__details {
				//background: rgba(cyan, .35);
				height: 100%;
				
				display: flex;
				justify-content: center;
				align-items: center;
				
				padding: 4px;
			}
			
			.list-item__number {
				img {
					display: block;
					height: 19.444444vh;
				}
				
				svg {
					display: block;
					height: 19.444444vh;
					
					path {
						fill: transparent;
						stroke: rgba(black, .48);
						
						stroke-dasharray: 1200;
						stroke-dashoffset: 1200;
					}
				}
			}
			
			.list-item__details {
				padding-right: 8.88888vh;
				
				p {
					margin: 0;
					color: $content-light-default;
				}
			}
		}
		
		.grid-child--1 {
			grid-column-start: 2;
			grid-column-end: 4;
			
			grid-row-start: 2;
			grid-row-end: 2;
		}
		
		.grid-child--2 {
			grid-column-start: 3;
			grid-column-end: 5;
			
			grid-row-start: 1;
			grid-row-end: 1;
		}
		
		.grid-child--3 {
			grid-column-start: 4;
			grid-column-end: 6;
			
			grid-row-start: 2;
			grid-row-end: 2;
			
			background: $content-dark;
		}
		.grid-child--5 {
			grid-column-start: 6;
			grid-column-end: 8;
			
			grid-row-start: 2;
			grid-row-end: 2;
		}
		
		.grid-child--4 {
			grid-column-start: 5;
			grid-column-end: 7;
			
			grid-row-start: 1;
			grid-row-end: 1;
		}
	}
	
	
	// SECTION 2
	.slide-2--s2 {
		//margin-right: 500px;
		padding-top: 0;
		
		//background: lightcoral;
		
		display: flex;
		flex-direction: row;
		align-items: stretch;
		
		
		// TITLE
		.s2__title {
			padding-top: 0;
			
			//width: 106.25vh;
			margin-right: 17.22vh;
			
			display: grid;
			
			grid-template-rows: 50vh 50vh;
			grid-template-columns: 106.25vh;
			
			
			.title__image {
				display: flex;
				justify-content: flex-end;
				
				position: relative;
				
				img {
					height: 100%;
					width: 100%;
					object-fit: contain;
				}
				
				&::after {
					display: block;
					position: absolute;
					content: '';
					
					top: 65%;
					left: 21.5%;
					
					border: 12.5vh solid $sgs-blue;
					border-top-color: transparent;
					border-left-color: transparent;
				}
			}
			
			.title__heading {
				display: flex;
				justify-content: center;
				align-items: center;
			}
		}
		
		// GRID-LIST
		.s2__custom-list {
			padding-top: 17vh;
			
			//width: 229.1666vh;
			grid-gap: 4px;
			
			//background: lightcoral;
			
			display: grid;
			grid-template-rows: 83vh;
			grid-template-columns: 80px 280px 160px 350px 110px 270px 310px;
			
			.custom-list__item {
				//background: rgba(cyan, .25);
				
				overflow: visible;
				
				position: relative;
			}
			
			
			.list-item-- {
				&1 {
					.custom-item__text {
						top: 0;
					}
				}
				
				&2 {
					.custom-item__text {
						bottom: 25.5%;
					}
				}
				
				&3 {
					.custom-item__text {
						bottom: 60%;
					}
				}
				
				&4 {
					.custom-item__text {
						bottom: 27.5%;
					}
				}
				
				&5 {
					.custom-item__text {
						top: 6.6%;
					}
				}
				
				&6 {
					.custom-item__text {
						bottom: 25%;
					}
				}
				
				&7 {
					.custom-item__text {
						bottom: 64%;
					}
				}
			}
			
			
			.custom-item__text {
				position: absolute;
				//overflow: visible;
				
				p {
					margin: 0;
					display: block;
					white-space: nowrap;
				}
				
				.custom-item__deco {
					position: absolute;
					top: calc(100% + 24px);
					width: 41px;
					height: 36px;
					background-image: url("../../../static/images/slide2/custom-list-deco.svg");
					background-position: center top;
					background-size: contain;
					background-repeat: no-repeat;
					z-index: 10;
				}
				
				.custom-item__line {
					display: block;
					position: absolute;
					
					top: calc(100% + 24px);
					left: 20px;
					
					width: 1px;
					height: 100vh;
					z-index: 5;
					
					background: $light-grey;
				}
			}
		}
	}
}