.s3-ch3__list {
	min-width: 1500px;
	
	//background: lightgoldenrodyellow;
	
	padding-top: 0;
	
	display: flex;
	flex-direction: row;
	align-items: stretch;
	
	@mixin svg-width($cof) {
		width: calc(32vh * #{$cof});
	}
	
	.item--custom {
		//background: rgba(lightcoral, .35);
		
		margin-right: 16px;
		
		width: 115vh;
		min-width: 800px;
		
		padding-left: 22vh;
		//padding-top: 15vh;
		padding-right: 11vh;
		
		position: relative;
		
		display: flex;
		justify-content: center;
		align-items: center;
		
		.item__content {
			position: relative;
			
			display: grid;
			grid-gap: 7.777vh;
			grid-template-columns: auto auto;
			grid-template-rows: 1fr;
			
			//background: cyan;
			
			h3 {
				display: block;
				position: absolute;
				margin: 0;
				font-size: 4.444vh;
				
				top: 0;
				transform: translateY(calc(-100% - 10vh));
			}
			
			p {
				display: block;
				margin: auto 0;
				font-size: 20px;
				//margin: 0;
				color: $content-light-default;
				line-height: 1.6;
			}
			
			svg {
				height: 32vh;
				//width: calc(32vh * 0.51);
				@include svg-width(0.51);
				display: block;
				position: relative;
				
				margin: auto 0;
				
				//background: orange;
				
				path {
					stroke: $sgs-blue;
					fill: $sgs-blue;
					opacity: 1;
					fill-opacity: 1;
				}
			}
		}
	}
	
	// REGULAR ITEM
	.item {
		//background: rgba(lightcoral, .35);
		
		//margin-right: 16px;
		
		min-width: 500px;
		width: 77.777vh;
		
		position: relative;
		
		display: flex;
		flex-direction: column;
		
		.item__img {
			background: rgba(cyan, .35);
			height: 50%;
			overflow: hidden;
			
			img {
				display: block;
				object-fit: cover;
				height: 100%;
				width: 100%;
			}
		}
		
		.item__text {
			height: 50%;
			
			font-size: 20px;
			color: white;
			
			padding: 7.777vh 11.111vh;
			
			display: flex;
			flex-direction: row;
			align-items: flex-end;
			
			background: $sgs-blue;
			
			p {
				display: block;
				margin: 0;
				line-height: 1.6;
			}
		}
		
		
		
		// SVG STYLES
		svg {
			display: block;
			position: absolute;
			
			left: 8.333vh;
			top: 50%;
			transform: translateY(-50%);
			
			//background: rgba(crimson, .35);
			
			height: 32vh;
			
			path {
				opacity: 1;
				fill-opacity: 1;
				
				stroke: white;
				fill: white;
			}
		}
		
		
		&--2 {
			svg {
				@include svg-width(0.84);
			}
		}
		&--3 {
			svg {
				@include svg-width(0.84);
			}
		}
		&--4 {
			svg {
				@include svg-width(1);
			}
		}
		&--5 {
			svg {
				@include svg-width(0.83);
			}
		}
		&--6 {
			svg {
				@include svg-width(0.85);
			}
		}
	}
	
	
	.item--alt {
		flex-direction: column-reverse;
		
		.item__text {
			align-items: flex-start;
			color: $content-light-default;
			background: white;
		}
		
		svg {
			path {
				stroke: $sgs-blue;
				fill: $sgs-blue;
			}
		}
	}
}