.slider__navigation {
	height: 80px;
	box-sizing: border-box;
	flex-shrink: 0;
	
	display: flex;
	flex-direction: row;
	
	justify-content: space-between;
	align-items: center;
	
	//background: rgba(crimson, .25);
}


// nav buttons
.slider__nav-buttons {
	//height: 100%;
	
	display: flex;
	flex-direction: row;
	align-items: stretch;
}

.slider__nav-btn {
	width: 80px;
	height: 80px;
	border: 0;
	font-size: 0;
	color: transparent;
	
	//background: transparent;
	//color: white;
	
	&:hover {
		//color: darkslategrey;
		background: rgba(white, .1);
	}
}

.nav-btn__prev {
	margin-right: 1px;
	position: relative;
	
	background:
		url("../../../static/images/slide-up.svg") no-repeat center center
		transparent;
	
	&:hover {
		background:
			url("../../../static/images/slide-up.svg") no-repeat center center
			rgba(white, .1);
	}
	
	&::after {
		position: absolute;
		content: '';
		width: 1px;
		background: rgba(white, .16);
		
		top: 0;
		bottom: 0;
		right: -1px;
	}
}

.nav-btn__next {
	background:
		url("../../../static/images/slide-down.svg") no-repeat center center
		transparent;
	
	&:hover {
		background:
			url("../../../static/images/slide-down.svg") no-repeat center center
			rgba(white, .1);
	}
}

// slider Indicator
.slider__indicator {
	display: flex;
	flex-direction: row;
	align-items: flex-end;
	min-height: 16px;
	
	.indicator__item {
		width: 2px;
		font-size: 0;
		color: transparent;
		background: white;
		height: 2px;
		margin-right: 16px;
		border-radius: 4px;
		
		transition: height $trans-fast;
		
		&--active {
			height: 16px;
			//border-radius: 0;
		}
	}
}