.slide-3__chapter {
	background: $white;
	position: relative;
	//display: flex;
	flex-direction: row;
	
	display: none;
	
	&.slide-3__chapter--current {
		//animation-name: fade;
		//animation-duration: $trans-slow-half;
		
		display: flex;
		animation-name: fade;
		animation-duration: $trans-slow;
	}
	
	.chapter__hero {
		padding-left: 360px;
		width: 100vw;
		min-width: 1200px;
		height: 100%;
		position: relative;
		
		//margin-right: 1500px;
		//background: lightcoral;
		//background: lightgoldenrodyellow;
		
		.custom-ul {
			li {
				margin-bottom: 7px;
			}
		}
	}
	
	.chapter-hero__info {
		display: block;
		margin: 0 0 0 80px;
		
		width: 50%;
		
		//background: lightcoral;
		color: $content-light-subtle;
		line-height: 1.78;
		
		position: relative;
		z-index: 5;
		
		&::before {
			position: absolute;
			display: block;
			content: '';
			height: 2px;
			width: 56px;
			background: rgba(167, 174, 181, 0.6);
			
			left: -80px;
			top: 15px;
		}
		
		p {
			margin: 0 0 22px;
		}
	}
	
	h2 {
		display: block;
		position: relative;
		color: $ocean;
		font-size: 48px;
		letter-spacing: -0.024em;
		margin: 15px 0 30px;
		font-weight: bold;
		z-index: 10;
	}
	
	h3 {
		display: block;
		position: relative;
		color: $ocean;
		font-size: 32px;
	}
	
	.chapter-hero__image {
		position: absolute;
		
		bottom: 0;
		right: 0;
		
		width: 50vh;
		height: 50vh;
		
		transition:
			height 0ms,
			opacity $trans-slow-half,
			transform $trans-slow-half,
			width 0ms;
		
		//background: lightcoral;
		
		overflow: visible;
		
		//overflow: hidden;
		
		z-index: 1;
		
		.chapter-hero__img-wrapper {
			width: 50vh;
			height: 50vh;
			clip-path: polygon(100% 0, 0 100%, 100% 100%);
			
			position: relative;
		}
		
		
		
		
		
		.chapter-hero__image__deco-1 {
			display: block;
			position: absolute;
			
			left: 0;
			top: 0;
			
			width: 50vh;
			height: 50vh;
			
			box-sizing: border-box;
			
			border: 25vh solid white;
			border-bottom-color: transparent;
			border-right-color: transparent;
			
			z-index: 10;
		}
		
		
		.chapter-hero__image__deco-2 {
			display: block;
			position: absolute;
			
			left: 13.8888%;
			top: 13.8888%;
			
			width: 25vh;
			height: 25vh;
			
			box-sizing: border-box;
			
			border: 12.5vh solid $sgs-blue;
			border-top-color: transparent;
			border-left-color: transparent;
			
			z-index: 20;
		}
		
		&--bot {
			.chapter-hero__image__deco-2 {
				display: block;
				position: absolute;
				
				left: 0;
				top: 0;
				bottom: auto;
				
				width: 25vh;
				height: 25vh;
				
				box-sizing: border-box;
				
				border: 12.5vh solid $sgs-blue;
				border-top-color: transparent;
				border-left-color: transparent;
				
				z-index: 20;
			}
		}
		
		&--top {
			bottom: auto;
			top: 0;
			
			.chapter-hero__img-wrapper {
				clip-path: polygon(100% 0, 0 0, 100% 100%);
			}
			
			.chapter-hero__image__deco-1 {
				left: 0;
				top: 0;
				
				box-sizing: border-box;
				
				border: 25vh solid white;
				border-top-color: transparent;
				border-right-color: transparent;
			}
			
			
			.chapter-hero__image__deco-2 {
				left: 13.8888%;
				top: auto;
				bottom: 13.8888%;
				
				box-sizing: border-box;
				
				border: 12.5vh solid $sgs-blue;
				border-bottom-color: transparent;
				border-left-color: transparent;
			}
		}
		
		
		
		img {
			display: block;
			height: 150%;
			width: 200%;
			object-fit: cover;
			
			position: relative;
		}
	}
	
	.article-map {
		//background: rgba(lightcoral, .5);
		
		width: 100vh;
		height: 100%;
		min-width: 700px;
		
		padding: 80px 11vh 50px 11vh;
		
		.map-img {
			//background: cyan;
			position: relative;
			overflow: hidden;
			
			height: 46.666vh;
			
			margin-bottom: 9vh;
			
			img {
				display: block;
				object-fit: contain;
				width: 100%;
				height: 100%;
			}
		}
		
		p {
			display: block;
			border-top: 1px solid rgba(black, .1);
			padding-top: 2.5vh;
			
			color: $content-light-subtle;
			font-style: italic;
			line-height: 1.78;
			//font-size: 2.5vh;
		}
		
		&.article-map--blue {
			background: $sgs-blue;
			
			p {
				color: white;
				border: none;
				font-style: normal;
				//font-size: 24px;
				font-size: 3.1vh;
			}
		}
	}
}

.slide-3__nav__horizontal-lines {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	
	z-index: 10;
	
	.slide-3-nav__horizontal-line {
		height: 1px;
		background: rgba(167, 174, 181, 0.6);
		width: 100%;
		
		position: absolute;
		
		&:nth-of-type(1) {
			top: 24%;
		}
		&:nth-of-type(2) {
			top: 48%;
		}
		&:nth-of-type(3) {
			top: 80%;
		}
	}
}