.slider__nav-logo {
	object-fit: contain;
	width: 100%;
	height: 100%;
	// height: 0;
	// padding-bottom: 50%;
	// padding-top: 50%;
	
	display: flex;
	justify-content: center;
	align-items: center;
	
	// background: rgba($color: #1061c0, $alpha: .9);
	
	svg {
		width: 100%;
		height: 100%;
		object-fit: contain;
		
		path{
			transition: 400ms;
			fill: rgba($color: white, $alpha: .2);
			fill-opacity: 1;
			
			//stroke: rgba($color: #384461, $alpha: .5);
			stroke: rgba($color: black, $alpha: .2);
			&:hover {
				fill: rgba($color: white, $alpha: .3);
				//fill: rgba($color: #1061c0, $alpha: 1);
				cursor: pointer;
			}
			
			&.active-svg {
				fill: rgba($color: #1061c0, $alpha: 1);
				cursor: default;
			}
		}
	}
}