.slider__slide-description {
	flex-grow: 1;
	box-sizing: border-box;
	position: relative;
	//padding: 53px 64px;
	padding: 7vmin 8.8vmin;
	background: #1061C0;
	transition: background $trans-fast;
	
	
	
	.slide-description__content {
		//transition: opacity $trans-fast, visibility $trans-fast;
		//opacity: 0;
		//visibility: hidden;
		//position: absolute;
		//top: 53px;
		//bottom: 80px;
		//right: 64px;
		bottom: 11.11vmin;
		right: 8.8888vmin;
		left: 8.8888vmin;
		//overflow: hidden;
		
		&.current-description {
			opacity: 1;
			visibility: visible;
			
			animation-name: fadeIn;
			animation-duration: $trans-slow;
		}
	}
	
	h1 {
		font-family: $title-ff;
		text-transform: uppercase;
		//font-size: 48px;
		font-size: 5.66666vmin;
		line-height: 1.2;
		margin: 0 0 12px;
		
		&.fz-31 {
			//font-size: 31px;
			font-size: 4vmin;
		}
	}
	
	p {
		margin: 0 0 12px;
		line-height: 1.55;
		//font-size: 2.5vmin;
		color: rgba($color: white, $alpha: 0.8);
	}
	
	
	.slider__more-details {
		position: absolute;
		bottom: 0;
		right: 0;
		min-width: 50%;
		
		color: $content-light-default;
		
		text-align: left;
		
		font-family: $title-ff;
		font-weight: bold;
		text-transform: uppercase;
		font-size: 14px;
		line-height: 1;
		
		padding: 32px 32px 32px 0;
		
		border: 0;
		outline: 0;
		
		//background: white;
		background:
			url("../../../static/images/more-details-arrow.svg") no-repeat center right 24px
			white;
		cursor: pointer;
		transition:
			all $trans-fast,
			opacity $trans-slow,
			transform $trans-slow;
		
		span {
			background: white;
			padding-right: 8px;
			padding-left: 32px;
			transition: inherit;
		}
		
		&:hover {
			background:
				url("../../../static/images/more-details-arrow.svg") no-repeat center right 15px
				darken($color: white, $amount: 5);
			
			span {
				background: darken($color: white, $amount: 5);
			}
		}
	}
}