@keyframes fadeIn {
	from {
		opacity: 0;
		
		transform: translateY(10px);
	}
	
	to {
		opacity: 1;
		transform: translateY(0px);
	}
}

@keyframes fadeDownFullHeight {
	from {
		transform: translateY(-100%);
	}
	
	to {
		transform: translateY(0);
	}
}

@keyframes fade {
	from {
		opacity: 0;
	}
	
	to {
		opacity: 1;
	}
}

@keyframes dash {
	from {
		stroke-dashoffset: 1200;
	}
	to {
		stroke-dashoffset: 0;
	}
}