.main-screen__bg-video {
	position: absolute;
	z-index: 1;
	
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	
	background: #021034;
	
	opacity: 0;
	visibility: hidden;
	
	transition:
		opacity $trans-slow,
		visibility $trans-slow;
	
	&.active-bg-video {
		opacity: 1;
		visibility: visible;
	}
	
	video {
		width: 100%;
		height: 100%;
		
		object-fit: cover;
		
		opacity: .24;
	}
}