.menu-btn-block {
	position: fixed;
	top: 0;
	left: 0;
	height: 80px;
	display: flex;
	flex-direction: row;
	z-index: 10000;
	box-shadow: 4px 8px 24px rgba(0, 0, 0, 0.08);
	
	
	// display: none;
	
	.menu-btn {
		width: 80px;
		height: 80px;
		display: flex;
		justify-content: center;
		align-items: center;
		border: none;
		outline: none;
		
		background: white;
		font-weight: bold;
		cursor: pointer;
		transition: 200ms;
		
		&:hover {
			background: darken($color: white, $amount: 5);
		}
		
		.menu-btn__wrapper {
			//background: rgba(crimson, .35);
			width: 28px;
			height: 28px;
			position: relative;
			
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			
			.menu-btn__line {
				display: block;
				position: absolute;
				background: $dark-grey;
				height: 2px;
				border-radius: 2px;
				
				transition: all $trans-fast;
				
				transform-origin: center center;
			}
			
			.menu-btn__line--1 {
				width: 16px;
				top: 6px;
				left: 0;
			}
			.menu-btn__line--2 {
				width: 28px;
			}
			.menu-btn__line--3 {
				width: 16px;
				bottom: 6px;
				right: 0;
			}
		}
		
		
		&.menu-btn--close {
			.menu-btn__line--1 {
				transform: rotate(45deg);
				top: 9px;
				left: 2px;
			}
			.menu-btn__line--2 {
				transform: rotate(-45deg);
			}
			.menu-btn__line--3 {
				transform: rotate(45deg);
				bottom: 9px;
				right: 2px;
			}
		}
	}
}