.s4-section--5 {
	display: flex;
	flex-direction: row;
	padding: 11vh 40px 11vh 80px;
	
	p {
		padding: 0;
		max-width: 100%;
		
		
		&::before {
			display: none;
		}
	}
	
	& > div {
		//background: antiquewhite;
	}
	
	.map {
		flex-shrink: 0;
		width: 50%;
		margin-right: 7%;
		
		.map-img {
			//background: lightcoral;
			
			display: flex;
			justify-content: center;
		}
		
		p {
			display: block;
			border-top: 1px solid rgba(black, .1);
			padding-top: 18px;
			font-style: italic;
			color: $content-light-subtle;
			
			margin-right: 6%;
		}
	}
	
	.article__wrapper {
		width: 37%;
		
		padding: 4vw;
		flex-shrink: 0;
		background: $sgs-blue;
		
		display: flex;
		align-items: center;
		
		overflow: hidden;
		
		p {
			color: white;
			font-size: 24px;
			line-height: 1.5;
		}
	}
}