.main-menu {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	
	outline: none;
	
	overflow-y: hidden;
	
	z-index: 5000;
	
	background: $modal-bg;
	color: #fff;
	
	transition: all $trans-slow-half;
	
	transform: translateY(-100%);
	
	display: flex;
	flex-direction: row;
	
}

.main-menu--open {
	transform: translateY(0%);
	
	//h1 {
	//	margin-top: 100px;
	//}
}

.main-menu__nav, .main-menu__contacts {
	flex-shrink: 0;
	width: 50%;
	
	//background: rgba(crimson, .3);
}


// menu nav
.main-menu__nav {
	padding-top: 80px;
	padding-left: 80px;
	display: flex;
	flex-direction: column;
	
	position: relative;
	
	
	transition:
			opacity $trans-slow-half,
			transform $trans-slow-half;
	opacity: 0;
	transform: translateY(20px);
	
	& > div {
		//background: rgba(crimson, .3);
		//box-shadow: inset 0 0 0 2px yellow;
	}
}

.main-menu--open {
	.main-menu__nav {
		transition-delay: $trans-slow-half;
		
		opacity: 1;
		transform: translateY(0);
	}
}

.main-menu__nav-list {
	flex-grow: 1;
	
	padding-top: 48px;
	padding-right: 90px;
	
	display: flex;
	flex-direction: column;
	
	
	
	.main-menu__nav-item {
		color: white;
		//text-decoration: none;
		text-decoration-color: transparent;
		font-family: $title-ff;
		font-weight: 600;
		font-size: 20px;
		
		padding: 0;
		margin-bottom: 24px;
		
		transition: all $trans-fast;
		
		span {
			transition: $trans-slow-half;
		}
		
		&:hover {
			//text-decoration-color: inherit;
			
			span {
				//background: white;
				//color: $sgs-blue;
				opacity: .5;
			}
		}
	}
	
	.menu__active-slide {
		opacity: .5;
		pointer-events: none;
	}
	
	// SubItem Menu
	.nav-list__sub-item {
		text-decoration: none;
		transition: $trans-slow-half;
		color: rgba(white, .8);
		
		&:hover {
			//color: rgba(white, 1);
			opacity: .5;
		}
		
		&.slide-3__active-chapter {
			opacity: .5;
			pointer-events: none;
		}
	}
}

.main-menu__copyright {
	flex-shrink: 0;
	min-height: 80px;
	
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	
	.copyright__company {
		opacity: .8;
	}
}

.copyright__developer {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 14px 28px;
	position: relative;
	
	background: $ocean;
	//bottom: -2px;
	height: 100%;
	
	span {
		font-size: 14px;
		opacity: .8;
	}
	
	a {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		color: transparent;
		//font-size: 0;
		
		background: url("../../../static/images/miltiways-logo.svg") no-repeat center center;
	}
}


// contacts
.main-menu__contacts {
	background: #ffffff;
	color: #3D3D3D;
	
	transition:
		transform $trans-slow-half;
	
	position: relative;
	
	transform: translateY(-100%);
	
	display: flex;
	flex-direction: column;
}


.contacts__header {
	//background: antiquewhite;
	
	display: flex;
	flex-direction: column;
	
	opacity: 0;
	
	transition: opacity $trans-slow-half;

	
	.contacts__title {
		height: 80px;
		
		display: flex;
		align-items: center;
		
		padding: 10px 40px;
		
		border-bottom: 1px solid rgba(black, .1);
	}
	
	.contacts__data {
		display: flex;
		flex-direction: row;
		
		.contacts__phone {
			border-right: 1px solid rgba(black, .1);
		}
		
		.contacts__phone, .contacts__email {
			width: 50%;
			height: 80px;
			
			display: flex;
			justify-content: center;
			align-items: center;
			
			flex-shrink: 0;
			flex-grow: 0;
			
			a {
				width: 100%;
				height: 100%;
				
				display: flex;
				justify-content: center;
				align-items: center;
				
				text-decoration: none;
				color: $content-light-default;
				background: transparent;
				
				transition: background $trans-slow-half;
				
				&:hover {
					background: rgba($content-light-default, .1);
				}
			}
		}
	}
}

.contacts__geo {
	background: antiquewhite;
	flex-grow: 1;
	position: relative;
	
	opacity: 0;
	transition: opacity $trans-slow-half;
	
	
	.contacts__map {
		position: absolute;
		
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		
		z-index: 5;
		
		background:
			url("../../../static/images/menu-map.jpg") no-repeat center center / cover;
		
		iframe {
			opacity: 0;
			display: none;
		}
	}
	
	.contacts__address {
		position: absolute;
		z-index: 20;
		
		bottom: 0;
		left: 0;
		
		background: white;
		
		width: 75%;
		
		padding: 35px 40px;
		
		a {
			text-decoration-color: $content-light-default;
			display: inline-block;
			margin-left: 8px;
			color: $content-light-default;
			
			transition: $trans-slow-half;
			
			&:hover {
				text-decoration-color: $sgs-blue;
				color: $sgs-blue;
			}
		}
	}
}

.main-menu--open {
	.main-menu__contacts {
		transition-delay: 200ms;
		transform: translateY(0%);
		
		.contacts__header, .contacts__geo {
			transition-delay: $trans-slow;
			opacity: 1;
		}
	}
}