.slide-3__chapter-2 {
	.article-map {
		.map-img {
			height: 46.666vh;
		}
		
		img {
			mix-blend-mode: lighten;
		}
	}
	
	.s3-ch2__range {
		//margin-right: 1000px;
		padding: 80px 80px 0 80px;
		
		//background: lightgoldenrodyellow;
		
		display: flex;
		flex-direction: row;
		align-items: stretch;
	}
	
	.range__grid-col {
		//background: rgba(crimson, .3);
		
		position: relative;
		
		display: flex;
		flex-direction: column;
		
		.range__grid-item {
			position: relative;
			margin-right: 0;
			margin-left: auto;
			
			width: 38.888vh;
			min-width: 280px;
			
			h4 {
				position: relative;
				//color: $sgs-blue;
				font-size: 2.222vh;
				line-height: 1.5;
				display: block;
				margin: 0;
				//font-size: 16px;
			}
			
			.range__img-wrapper {
				position: relative;
				height: 22.222vh;
				min-height: 160px;
				
				width: 38.888vh;
				min-width: 280px;
				
				overflow: hidden;
				
				margin-bottom: 2.222vh;
				
				img {
					display: block;
					object-fit: cover;
					width: 100%;
					height: 100%;
				}
			}
		}
		
		h2 {
			//margin-bottom: 53px;
			margin-bottom: 7.4vh;
		}
		
		&--1 {
			min-width: 380px;
			width: 52.777vh;
			//margin-right: 115px;
			margin-right: 16vh;
		}
		&--2 {
			min-width: 350px;
			width: 48.6vh;
			//margin-right: 70px;
			margin-right: 10vh;
			
			.range__grid-item:nth-of-type(2) {
				margin-right: auto;
				margin-left: 0;
				margin-top: 16.666vh;
			}
		}
		&--3 {
			min-width: 280px;
			width: 39vh;
			//margin-right: 115px;
			margin-right: 16vh;
			
			padding-bottom: 30px;
			justify-content: center;
		}
		&--4 {
			min-width: 410px;
			width: 57vh;
			//margin-right: 32px;
			margin-right: 4.444vh;
			
			.range__grid-item:nth-of-type(1) {
				margin-right: auto;
				margin-left: 0;
				margin-bottom: 11.111vh;
			}
		}
		&--5 {
			min-width: 280px;
			width: 39vh;
		}
	}
}