// SLIDE NAVIGATION
.slide-3__nav {
	background: $content-dark;
	color: $content-light-subtle;
	
	width: 280px;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	
	z-index: 550;
	
	//opacity: .1;
	
	//24+24+32+20
	
	.s3__nav-wrapper {
		//background: rgba(lightcoral, .5);
		height: 100%;
		box-sizing: border-box;
		position: relative;
		
		display: grid;
		grid-gap: 1px;
		grid-template-columns: 100%;
		grid-template-rows: 24% 24% 32% 20%;
		
		border-top: 1px solid rgba(167, 174, 181, 0.6);
		border-bottom: 1px solid rgba(167, 174, 181, 0.6);
		
		.s3__nav-indicator {
			background: white;
			position: absolute;
			border-radius: 4px;
			
			z-index: 30;
			
			transition:
				top $trans-slow-half,
				height $trans-slow-half;
			
			height: calc(24% - 16px);
			left: 8px;
			right: 8px;
			top: calc(0% + 8px);
		}
	}
	
	.slide-3__nav-item {
		//background: rgba(lightcoral, .1);
		//background: $content-dark;
		padding: 8px;
		z-index: 50;
		
		.s3__nav-item__btn {
			display: block;
			height: 100%;
			width: 100%;
			text-align: left;
			font-size: 16px;
			
			padding: 3.3vh;
			
			border-radius: 4px;
			background: transparent;
			
			transition: all $trans-fast;
			
			overflow: hidden;
			
			&:hover {
				background: rgba(white, .5);
			}
		}
	}
	
	.slide-3-nav__active-item {
		.s3__nav-item__btn {
			cursor: default;
			pointer-events: none;
			font-weight: bold;
			color: $content-light-default;
		}
	}
}