.go-back-btn {
	z-index: 9000;
	position: fixed;
	left: 0;
	opacity: 1;
	height: 80px;
	width: 80px;
	box-sizing: border-box;
	
	display: flex;
	align-items: center;
	justify-content: center;
	
	border: 0;
	color: transparent;
	
	transition:
		left $trans-fast linear 0ms,
		opacity $trans-fast linear 0ms,
		background $trans-fast;
	
	background:
		url("../../../static/images/back-icon--white.svg") no-repeat center center
		$sgs-blue;
	
	&:hover {
		background:
			url("../../../static/images/back-icon--white.svg") no-repeat center center
			darken($sgs-blue, 5);
	}
	
	&--active {
		left: 80px;
		opacity: 1;
		transition:
			left $trans-fast linear 560ms,
			opacity $trans-fast linear 560ms,
			background $trans-fast;
	}
}