.content__slide-4 {
	//width: 100vw;
	height: 100vh;
	overflow-x: hidden;
	position: absolute;
	
	display: flex;
	flex-direction: row;
	
	transition: transform $trans-slow;
	
	//left: calc((100vw + 40px + 80px) * 0);
	
	transform: translateX(calc((100vw + 120px) * -1 * 1));
}

.s4-section {
	position: relative;
	//background: antiquewhite;
	outline: none !important;
	//background: lightcoral;
	
	width: calc(100vw + 40px);
	
	//margin-right: 80px;
	padding-left: 80px;
	
	h2 {
		max-width: 50%;
		margin-top: 13.8vh;
		position: relative;
		font-size: 48px;
		color: $ocean;
		display: block;
		line-height: 1.17;
		font-weight: bold;
		letter-spacing: -0.024em;
		
		z-index: 20;
	}
	
	p {
		display: block;
		max-width: 50%;
		z-index: 15;
		position: relative;
		
		color: $content-light-subtle;
		line-height: 1.78;
		//mix-blend-mode: color-dodge;
		
		padding-left: 80px;
		
		&::before {
			display: block;
			content: '';
			height: 2px;
			width: 56px;
			
			background: rgba(167, 174, 181, 0.6);;
			
			position: absolute;
			left: 0;
			top: 16px;
		}
	}
	
	.img__deco--large {
		position: absolute;
		//z-index: -1;
		
		width: 100vh;
		height: 100vh;
		
		top: 0;
		bottom: 0;
		right: 40px;
		
		box-sizing: border-box;
		
		border: 50vh solid $dark-ocean;
		border-bottom-color: transparent;
		border-left-color: transparent;
	}
	
	.img__deco--small {
		position: absolute;
		//z-index: -1;
		
		width: 66.666vh;
		height: 66.666vh;
		
		top: 16.666vh;
		bottom: 0;
		right: 0;
		
		box-sizing: border-box;
		
		border: 33.333vh solid $ocean;
		border-bottom-color: transparent;
		border-left-color: transparent;
	}
	
	.s4__img--secondary {
		position: absolute;
		//z-index: -1;
		opacity: 0;
		visibility: hidden;
		transition: opacity $trans-slow-half, visibility $trans-slow-half;
		
		width: 66.666vh;
		height: 66.666vh;
		
		top: 16.666vh;
		bottom: 0;
		right: 0;
		
		box-sizing: border-box;
		
		//border-bottom: 33.333vh solid red;
		//border-left: 33.333vh solid red;
		//border-top: 33.333vh solid transparent;
		//border-right: 33.333vh solid transparent;
		
		
		
		.img-deco-small {
			display: block;
			position: absolute;
			content: '';
			
			height: 33.333vh;
			width: 33.333vh;
			
			transition:
				top $trans-slow-half,
				left $trans-slow-half;
			
			top: 55%;
			left: -5%;
			
			
			background: $ocean;
			clip-path: polygon(100% 0, 0 0, 100% 100%);
			
			//border: 16.665vh solid $ocean;
			//border-bottom-color: transparent;
			//border-left-color: transparent;
			
			&.deco-sgs-blue {
				background: $sgs-blue;
			}
		}
		
		img {
			height: 100%;
			width: 100%;
			object-fit: cover;
			
			clip-path: polygon(100% 0, 0 0, 100% 100%);
		}
	}
	
	.s4__img-secondary--active {
		transition: opacity $trans-slow, visibility $trans-slow;
		opacity: 1;
		visibility: visible;
		
		.img-deco-small {
			transition:
				top $trans-slow-half,
				left $trans-slow-half;
			
			top: 50%;
			left: 0;
		}
	}
	
	.s4__item-img {
		position: absolute;
		z-index: 10;
		//background: rgba(firebrick, 1);
		
		color: white;
		font-weight: bold;
		
		width: 100vh;
		padding-top: 60px;
		
		top: 0;
		right:40px;
		bottom: 0;
		
		opacity: 0;
		visibility: hidden;
		
		transition: opacity $trans-slow-half, visibility $trans-slow-half;
		
		.img-wrapper {
			//background: rgba(cyan, 1);
			
			position: absolute;
			top: 60px;
			right: 0;
			
			//width: calc(100vh);
			width: 80%;
			margin-right: 0;
			margin-left: auto;
			//padding-right: 40px;
			
			height: calc(100vh - 240px - 60px);
			
			img {
				height: 100%;
				width: 100%;
				object-fit: contain;
				
				display: block;
				
				margin-left: auto;
				margin-right: 0;
				
				transition: $trans-slow-half;
				
				transform: translateY(-20px);
				
				//background: orange;
			}
		}
	}
	
	.s4__item-img--active {
		opacity: 1;
		visibility: visible;
		transition: opacity $trans-slow, visibility $trans-slow;
		
		
		img {
			transform: translateY(0) !important;
			transition: $trans-slow;
		}
	}
}