.s4__section-navigation {
	background: $content-dark;
	overflow-x: hidden;
	
	z-index: 30;
	
	position: absolute;
	bottom: 0;
	right: 0;
	left: 80px;
	
	//height: 33.3vh;
	height: 240px;
	width: calc(100vw - 80px);
	
	//opacity: .2;
	
	.nav-header {
		height: calc(100% / 3);
		//background: orange;
		position: relative;
		
		text-transform: uppercase;
		font-size: 14px;
		font-weight: bold;
		letter-spacing: 0.04em;
		line-height: 1;
		color: $content-light-default;
		
		display: flex;
		align-items: center;
		
		justify-content: space-between;
		
		padding-left: 32px;
		
		border-bottom: 1px solid rgba(black, .12);
		
		.nav-buttons {
			//background: lightcoral;
			height: 100%;
			
			display: flex;
			align-items: stretch;
			
			.nav__button {
				//background: transparent;
				//background: antiquewhite;
				position: relative;
				display: block;
				font-size: 0;
				color: transparent;
				
				transition: $trans-fast;
				
				width: calc(240px / 3);
				
				&:hover {
					background: rgba($content-light-default, .1);
				}
			}
			
			.button-prev {
				background:
					url("../../../../static/images/slide4/nav-left-arrow.svg") no-repeat center center
					transparent;
				
				&:hover {
					background:
						url("../../../../static/images/slide4/nav-left-arrow.svg") no-repeat center center
						rgba($content-light-default, .1);
				}
			}
			
			.button-next {
				background:
					url("../../../../static/images/slide4/nav-right-arrow.svg") no-repeat center center
					transparent;
				
				&:hover {
					background:
						url("../../../../static/images/slide4/nav-right-arrow.svg") no-repeat center center
						rgba($content-light-default, .1);
				}
			}
			
			.button-prev {
				margin-right: 1px;
				
				&::after {
					position: absolute;
					display: block;
					content: '';
					
					width: 1px;
					top: 0;
					bottom: 0;
					right: -1px;
					
					background: rgba($content-light-default, .16);
				}
			}
		}
	}
	
	.section-nav__main {
		height: calc(100% / 3 * 2);
		position: absolute;
		overflow-x: hidden;
	}
	
	.nav-list {
		height: 100%;
		display: flex;
		flex-direction: row;
		position: relative;
		z-index: 30;
		
		transition: transform $trans-slow-half, left $trans-slow-half;
		
		transform: translateX(calc(((100vw - 80px) / 3) * -1 * 0 - 1px));
	}
	
	.s4__list-item {
		//background: lightcoral;
		flex-shrink: 0;
		flex-grow: 0;
		position: relative;
		
		
		cursor: default;
		
		width: calc((100vw - 80px) / 3);
		height: 100%;
		//width: 33%;
		
		padding: 12px;
		//margin-right: 1px;
		
		display: flex;
		align-items: stretch;
		box-sizing: border-box;
		
		color: $content-light-subtle;
		
		//transition: font-weight $trans-slow-half;
		
		&.s4__list-item--active {
			font-weight: bold;
			color: $dark-ocean;
			
			transition-delay: $trans-slow-half / 2;
		}
		
		span {
			//background: cyan;
			//position: absolute;
			//top: 12px;
			//left: 12px;
			display: block;
			width: 100%;
			border-radius: 4px;
			box-sizing: border-box;
			
			padding: 14px 20px;
		}
	}
	
	.s4__nav-indicator {
		//background: rgba(cyan, .25);
		top: 0;
		position: absolute;
		z-index: 20;
		width: calc((100vw - 80px) / 3);
		padding: 12px;
		height: 100%;
		
		transition: left $trans-slow-half;
		
		left: calc((100% / 3) * 0);
		
		span {
			background: white;
			display: block;
			width: 100%;
			height: 100%;
			border-radius: 4px;
			box-sizing: border-box;
		}
	}
	
	.section-nav__vertical-lines {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		
		height: 100%;
		
		//width: 100%;
		
		transition: transform $trans-slow-half;
	}
	
	.s4__nav-vertical-line {
		display: block;
		position: absolute;
		z-index: 10;
		
		top: 0;
		bottom: 0;
		left: 0;
		
		width: 1px;
		
		background: rgba(black, .12);
		
		&:nth-of-type(1) {
			left: calc(((100vw - 80px) / 3) * 1);
		}
		&:nth-of-type(2) {
			left: calc(((100vw - 80px) / 3) * 2);
		}
		&:nth-of-type(3) {
			left: calc(((100vw - 80px) / 3) * 3);
		}
		&:nth-of-type(4) {
			left: calc(((100vw - 80px) / 3) * 4);
		}
		&:nth-of-type(5) {
			left: calc(((100vw - 80px) / 3) * 5);
		}
		&:nth-of-type(6) {
			left: calc(((100vw - 80px) / 3) * 6);
		}
		&:nth-of-type(7) {
			left: calc(((100vw - 80px) / 3) * 7);
		}
		&:nth-of-type(8) {
			left: calc(((100vw - 80px) / 3) * 8);
		}
	}
}