.slide-3__chapter-4 {
	//padding-right: 1000px;
	
	.chapter-hero__image {
		.chapter-hero__img-wrapper {
			//clip-path: none;
			//top: -150px;
			//background: red;
		}
		
		img {
			height: 100vh;
			width: auto;
			
			position: absolute;
			left: 0%;
			bottom: 0;
		}
	}
	
	
	
	.s3-ch4__deco {
		height: 50vh;
		width: 50vh;
		
		position: relative;
		//overflow: hidden;
		transition:
			height 0ms,
			opacity $trans-slow-half,
			transform $trans-slow-half,
			width 0ms;
		
		clip-path: polygon(0 0, 0% 100%, 100% 0);
		
		img {
			display: block;
			object-fit: cover;
			
			position: absolute;
			top: 0;
			left: -100%;
			
			
			height: 100vh;
			//width: 100%;
		}
	}
	
	// LIST GRID
	.s3-ch4__list {
		display: grid;
		//grid-gap: 4px;
		grid-template-rows: 50% 50%;
		grid-template-columns: 50vh 50vh 27.777vh 50vh 50vh 27.777vh 50vh ;
		
		grid-template-areas:
				"title item2 item2 item2 item4 item4 item4"
				"item1 item1 item1 item3 item3 item3 ."
				;
		
		//background: palevioletred;
		
		.list-title {
			//background: lightcoral;
			display: flex;
			align-items: center;
			
			grid-area: title;
			
			h2 {
				margin: 0;
			}
		}
		
		
		// 115px svg height --> 16vh
		@mixin svg-width($cof) {
			width: calc(16vh * #{$cof});
		}
		.item {
			//background: lightgoldenrodyellow;
			
			display: flex;
			flex-direction: row;
			align-items: center;
			overflow: hidden;
			
			svg {
				height: 16vh;
				
				path {
					height: 100%;
					stroke: $dark-grey;
					
					fill: transparent;
					fill-opacity: 1;
					opacity: .48;
					
					stroke-dasharray: 1200;
					stroke-dashoffset: 1200;
				}
			}
			
			&--1 {
				//grid-row-start: 2;
				//grid-row-end: 2;
				//
				//grid-column-start: 1;
				//grid-column-end: 1;
				
				grid-area: item1;
				
				svg {
					@include svg-width(.51)
				}
			}
			&--2 {
				//grid-row-start: 1;
				//grid-row-end: 1;
				//
				//grid-column-start: 2;
				//grid-column-end: 2;
				
				grid-area: item2;
				
				svg {
					@include svg-width(.84)
				}
			}
			&--3 {
				//grid-row-start: 2;
				//grid-row-end: 2;
				//
				//grid-column-start: 2;
				//grid-column-end: 2;
				
				grid-area: item3;
				
				svg {
					@include svg-width(.84)
				}
			}
			&--4 {
				//grid-row-start: 1;
				//grid-row-end: 1;
				//
				//grid-column-start: 3;
				//grid-column-end: 3;
				
				grid-area: item4;
				
				svg {
					@include svg-width(1)
				}
			}
		}
		
		.item__img {
			height: 50vh;
			width: 50vh;
			overflow: hidden;
			flex-shrink: 0;
			
			img {
				display: block;
				object-fit: cover;
				height: 100%;
				width: 100%;
			}
		}
		
		.item__content {
			display: flex;
			flex-direction: column;
			justify-content: center;
			
			//background: cyan;
			
			height: 100%;
			width: 100%;
			
			padding: 3vh 11vh 9.7vh 13vh;
			
			svg {
				display: block;
				margin-bottom: 5vh;
			}
			
			p {
				display: block;
				margin: 0;
				line-height: 1.5;
				color: $content-light-default;
			}
		}
	}
}