html {
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  outline: none;
}

body {
  letter-spacing: -.02rem;
  font-family: $body-ff;
  font-size: 18px;
  line-height: 1.5;
  box-sizing: border-box;
  background: $dark-ocean;

  // background: rgba($color: #000000, $alpha: 0.5);
  display: flex;
  // flex-direction: row;
  height: 100%;
  // align-items: stretch;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.hero {
  width: 100%;
  height: 100%;
  
  overflow-x: visible;
  
  //background: green;
  
  display: flex;
  flex-direction: row;
  align-items: stretch;
  
  position: relative;
  left: 0;
  
  transition: left $trans-slow;
  
  &.transition-disabled {
    transition: none !important;
  }
  
  &[data-viewport="mainScreen"] {
    left: 0;
  }
  
  &[data-viewport="slideContent"] {
    left: -100vw;
  }
}

button {
  border: none;
  outline: none;
  cursor: pointer;
  transition: 200ms;
  
  &:hover {
    background: darken($color: white, $amount: 5);
  }
}


.sgs-blue-bg {
  background: $sgs-blue;
}

.custom-ul {
  margin: 0;
  padding: 0;
  
  display: flex;
  flex-direction: column;
  
  list-style-type: none;
  
  //background: rgba(crimson, .25);
  
  li {
    //background: rgba(cyan, .25);
    
    padding-left: 64px;
    margin-bottom: 20px;
  
    position: relative;
    
    &::before {
      position: absolute;
      content: '';
      left: 0;
      top: 2px;
      width: 20px;
      height: 22px;
      background: url("../../static/images/list-deco-blue.svg") no-repeat center center;
    }
  }
  
  &--skeleton {
    margin: 7px 0 0;
    padding: 0;
    
    
    li {
      //background: rgba(cyan, .25);
    
      padding-left: 30px;
      margin-bottom: 5px;
    
      position: relative;
    
      &::before {
        position: absolute;
        content: '';
        left: 0;
        top: 8px;
        width: 14px;
        height: 14px;
        background: url("../../static/images/list-deco-skeleton.svg") no-repeat center center;
      }
    }
  }
}

.overflow-x--hidden {
  overflow-x: hidden;
}