.main-screen {
	//background: url('../../../static/images/image 13.png') no-repeat center center / cover, #021034;
	background: #021034;
	
	flex-shrink: 0;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
	color: white;
	
	overflow: hidden;
	
	display: flex;
	flex-direction: row;
	position: relative;
	outline: none;
}

.description {
	position: absolute;
	background: rgba(darken(firebrick, 50), .7);
	transition: all 640ms;
	z-index: 10;
	display: none;
	
	&:hover {
		opacity: 0;
		visibility: hidden;
	}
}

.page-logo {
	background: white;
	height: 80px;
	width: 142px;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	//padding: 20px 25px;
	position: absolute;
	top: 0;
	left: 80px;
	z-index: 6000;
	//animation-name: fade;
	//animation-duration: $trans-slow;
	
	&--fixed {
		position: fixed;
		z-index: 9500;
	}
	
	a {
		//box-shadow: inset 0 8px 12px rgba(0, 0, 0, 0.08);
		font-size: 0;
		padding: 20px 25px;
		width:  100%;
		height: 100%;
		object-fit: contain;
		display: flex;
		justify-content: center;
		align-items: center;
		background: url('../../../static/images/sgs-logo.svg') no-repeat center center;
		
		img {
			padding: 1px;
			display: block;
			object-fit: contain;
		}
	}
}

.main-screen__side {
	width: 50%;
	box-sizing: border-box;
	flex-shrink: 0;
	display: flex;
	//align-items: center;
	z-index: 10;
}

.main-screen__left-side {
	padding: 125px 82px 45px;
}

.main-screen__right-side {
	//background: rgba(firebrick, .3);
	
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: stretch;
}