// Roboto
@font-face {
    font-family: "RobotoFont";
    src: url('../../static/fonts/Roboto-Regular.ttf');
    
    font-style: normal;
    font-weight: normal;
}
@font-face {
    font-family: "RobotoFont";
    src: url('../../static/fonts/Roboto-Italic.ttf');
    
    font-style: italic;
    font-weight: normal;
}
@font-face {
    font-family: "RobotoFont";
    src: url('../../static/fonts/Roboto-Bold.ttf');
    font-style: normal;
    font-weight: bold;
}

